/* eslint-disable max-len */
import { Card, Space } from 'antd';
import 'antd/dist/antd.css';
import LineTo from 'react-lineto';
import React from 'react';
import data from './data';

function MatchLineTo() {
  const [state, setState] = React.useState({
    userSelect: [],
    userAnswers: [],
  });

  React.useEffect(() => {
    // setState((pre) => ({ ...pre }));
  }, []);

  const handleClick = React.useCallback((name, id) => {
    let { userSelect, userAnswers } = state;
    if (name === 'left') userSelect[0] = id;
    if (name === 'right') userSelect[1] = id;
    // connect two points
    if (userSelect[0] && userSelect[1]) {
      // ...find Repeat elements
      let indexRepeat = -1;
      do {
        indexRepeat = userAnswers.findIndex((item) => item.split('-').some((x, i) => x == userSelect[i])); // ('1'== 1)-->true
        // indexRepeat = userAnswers.findIndex((item) => item.split('-')[1] == userSelect[1]); // ('1'== 1)-->true
        if (indexRepeat > -1) userAnswers.splice(indexRepeat, 1); // remove Repeat element
      } while (indexRepeat > -1);
      //
      userAnswers.push(userSelect.join('-'));
      userSelect = []; // clear
    }
    setState((pre) => ({ ...pre, userSelect, userAnswers }));
  }, [state]);

  const renderLineTo = React.useCallback((arrayData) => {
    const Anchor = data.settings.horizontal ?
      { fromAnchor: 'center bottom', toAnchor: 'center top' } :
      { fromAnchor: 'center right', toAnchor: 'center left' };
    return arrayData.map((item, index) => {
      const [L, R] = item.split('-');
      return (
        <LineTo
          key={index}
          {...Anchor}
          from={`left-${L}`}
          to={`right-${R}`}
          within='container'
          borderColor='blue'
        />
      );
    });
  }, []);

  const renderList = React.useCallback((Data, name) => {
    if (!Data[name]) return null;
    return (
      <Space direction={data.settings.horizontal ? 'horizontal' : 'vertical'}>
        {
          Data[name].map((item, index) =>
            <Card
              key={item.id}
              className={`${name}-${item.id}`}
              bodyStyle={{ border: '1px solid', borderRadius: '3px', cursor: 'pointer' }}
              onClick={() => handleClick(name, item.id)}
            >
              {item.text}
            </Card>)
        }
      </Space>
    );
  }, [handleClick]);
  console.log(state);
  return (
    <div >
      <h1>{data.title}</h1>
      <div className='container' style={{ position: 'relative' }} >
        <Space size={200} direction={data.settings.horizontal && 'vertical'}>
          {renderList(data.question, 'left')}
          {renderList(data.question, 'right')}
        </Space>
        {/* {renderLineTo(data.answers)} */}
        {renderLineTo(state.userAnswers)}
      </div>
    </div >
  );
}


export default MatchLineTo;

