import React from 'react';
import { Button, Layout, Menu } from 'antd';
import { useLocation, Redirect, Route, Switch } from 'react-router-dom';
// import { LaptopOutlined } from '@ant-design/icons';
import routes from './routes';
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  DesktopOutlined,
} from '@ant-design/icons';

const { Header, Footer, Sider, Content } = Layout;
// const { SubMenu } = Menu;

function ExcerciseTypes(props) {
  const [state, setState] = React.useState({
    collapsed: false,
  });
  // const location = useLocation();
  // console.log('ðŸš€ ~ location', location)

  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.collapse) {
        return getRoutes(prop.views);
      }
      if (prop.layout === '/ExcerciseTypes') {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };
  return (
    <div>
      <Layout style={{ minHeight: '100vh' }}>
        <Header className='bg-secondary'>
          <h1 className='text-white'>Excercise Types</h1>
        </Header>
        <Layout>
          <Sider trigger={null} collapsible collapsed={state.collapsed}>
            <div className='d-flex justify-content-end'>
              <Button type="primary" onClick={() => setState((pre) => ({ ...pre, collapsed: !state.collapsed }))}>
                {React.createElement(state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
              </Button>
            </div>
            <Menu
              mode="inline"
              theme="dark"
              style={{ height: '100%', borderRight: 0 }}
            >
              {routes.map((item, index) => <Menu.Item key={index} icon={<DesktopOutlined />}><a href={item.layout + item.path}>{item.name}</a></Menu.Item>)}
            </Menu>

          </Sider>

          <Content className="d-flex justify-content-center">
            <Switch>
              {getRoutes(routes)}
              <Redirect from="*" to="/ExcerciseTypes" />
            </Switch>
          </Content>
        </Layout>
        {/* <Footer>Footer</Footer> */}
      </Layout>

    </div>
  );
}

export default ExcerciseTypes;

