import './App.css';
import ExcerciseTypes from './components/ExcerciseTypes'
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" render={(props) => <ExcerciseTypes {...props} />} />
        <Redirect from="*" to="/" />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
