import { Button, Form, Input } from 'antd';
import React from 'react';
import data from './data';


function FindTheMissingLetters(props) {
  const [form] = Form.useForm();
  const [state, setstate] = React.useState({
    Question: null,
  });
  //
  const createQuestion = React.useCallback((word) => {
    const Letters = word.split('');
    return Letters.map((item, index) => {
      let isInput = !Math.round(Math.random() + 0.2);
      if (item === ' ') isInput = false;
      return { id: index, answer: item, isInput };
    });
  }, []);
  //
  React.useEffect(() => {
    const Question = createQuestion(data.answer);
    setstate(pre => ({ ...pre, Question }));
  }, []);
  //
  const handleSubmit = React.useCallback(() => {
    const { Question } = state;
    const values = form.getFieldsValue();
    console.log('ðŸš€ ~ values', values)
  }, [state]);
  //
  const renderQuestion = React.useCallback(() => {
    const { Question } = state;
    if (!Question) return null;
    return Question.map((item, index) => {
      console.log(item);
      if (item.isInput) {
        return (
          <div key={item.id} style={{ display: 'inline-block', marginRight: '2px' }}>
            <Form.Item name={`${item.id}`}>
              <Input
                bordered={false}
                maxLength={1}
                style={{
                  width: 40,
                  height: '100%',
                  fontSize: 35,
                  padding: 2,
                  textAlign: 'center',
                  borderBottom: '2px solid',
                  color: 'green',
                }} />
            </Form.Item>
          </div>
        );
      }
      return item.answer;
    });
  }, [state]);

  return (
    <div>
      <h1>{data.title}</h1>
      <Form form={form}>
        <div style={{ fontSize: 35 }}>
          {renderQuestion()}
        </div>
      </Form>
      <Button onClick={handleSubmit}>Submit</Button>
    </div>
  );
}


export default FindTheMissingLetters;

